import './dip.less'
import { Link } from 'react-router-dom'
import Button from '../../components/Button/Button'
import backIcon from '../../assets/img/back.svg'
export default function DipOne({ lang }) {
  return (
    <div className='dip-box'>
      <div className='back-governance'>
        <Link to='/governance'>
          <div className='icon-back'>
            <img alt='' src={backIcon} />
            <span>{lang['back']}</span>
          </div>
        </Link>
        <div className='poll-detail'>
          {lang['poll-detail']}
        </div>
      </div>
      <div className='dip-box-info'>
        <div className='poll-dip-info'>
          <div className='dip-list-title'>
            <div className='title-text'>
              {lang['dip']}-002
                </div>
            <div className='dip-executed'>
              <div className='executed-bg'></div>  FINISHED
            </div>
          </div>
          <div className='dip-list-des'>
            {lang['governance-describe']}
          </div>
          <div className='dip-time'>
            {lang['dip-two-time']}
          </div>
        </div>
        <div className='dip-option'>
          <div className='option-check'>
            <div className='option-title'>
              <div className='option-check-text'>
                <input
                  type='radio'
                  name='option'
                  value='1'
                  id="I"
                />
                <label></label>
                <span className='option-title-text'>
                  {lang['governance-options-one-describe']}
                </span>
              </div>
              <div className='option-num'>
                142,989 {lang['deri-voting']} (9.1%)
              </div>
            </div>
            <div className='option-peng-bg ' style={{ width: '9.1%' }}></div>
          </div>
          <div className='option-check'>
            <div className='option-title'>
              <div className='option-check-text'>
                <input
                  type='radio'
                  name='option'
                  value='2'
                  id="II"
                  defaultChecked={true}
                />
                <label></label>
                <span className='option-title-text'>
                  {lang['governance-options-two-describe']}
                </span>
              </div>
              <div className='option-num'>
                1,440,582 {lang['deri-voting']} (90.9%)
              </div>
            </div>
            <div className='option-peng-bg' style={{ width: '90.9%' }}></div>
          </div>
          <div className='option-check'>
            <div className='option-title'>
              <div className='option-check-text'>
                <input
                  type='radio'
                  name='option'
                  value='3'
                  id="III"
                />
                <label ></label>
                <span className='option-title-text'>
                  {lang['governance-options-three']}
                </span>
              </div>
              <div className='option-num'>
                0 {lang['deri-voting']} (0%)
              </div>
            </div>
            <div className='option-peng-bg'></div>
          </div>
          <div className='vote-button'>
            <Button disabled={true}  label={lang['vote']} width='286' fontSize='18' />
          </div>
        </div>
        <div className='dip-option-des-rules'>
          <div className='description-close-open'>
            {lang['description']}
          </div>
          <div className='description-text'>
            <div>{lang['the-community-can-vote']}</div>
            <div className='ul-description'>
              {lang['governance-describe']}
              <br></br>
              {lang['governance-describe-two']}
            </div>
          </div>
          <div className='supplementary-notes'>
            <div className='notes-title'>
              {lang['supplementary-notes']}
            </div>
            <div className='notes-text'>
              {lang['governance-describe-three']}
              <a rel='noreferrer' target="_blank" href='https://docs.deri.io/mining-faq#what-do-i-harvest-for-liquidity-mining'>
                {lang['governance-describe-three-a']}
              </a>
              {lang['governance-describe-three-one']}
            </div>
          </div>
          <div className='vote-rules'>
            <div className='rules-title'>
              {lang['voting-rules']}
            </div>
            <div className='rules-text'>
              <div className='rules-margin'>
                {lang['vote-rules-one']}
                <ul className='no-margin'>
                  <li>{lang['vote-rules-one-describe-one']}</li>
                  <li>{lang['vote-rules-one-describe-two']}</li>
                  <li>{lang['vote-rules-one-describe-three']}</li>
                  <li>{lang['vote-rules-one-describe-four']}</li>
                </ul>
                {lang['vote-rules-one-describe-four']}
              </div>
              <div className='rules-margin'>
                {lang['vote-rules-two']}
              </div>
              <div className='rules-margin'>
                {lang['vote-rules-three']}
              </div>
              <div className='rules-margin'>
                {lang['vote-rules-four']}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}


